.container {
  padding: 2rem;
}

.tabs {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
  border-bottom: 3px solid blue;

}

.tabsOver {
  overflow-x: hidden;
  overflow-y: hidden;
}

.tabs,
li {
  padding: 0.2em 1em;
  margin: 0 1em;
}

.current {
  background: white;
  color: blue;
  font-weight: bold;
  border: 3px solid blue;
  border-bottom: 0;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;
  position: relative;
  top: 7px;
}

.content {
  padding: 1em 2em;
}

@media only screen and (max-width: 800px) {
  .tabs li {
    font-size: 10px;
  }

  .tabsOver {
    overflow-x: scroll;
    
  }



  
}


